html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

html {font-size: calc(5px + (10 - 5) * (100vw - 320px) / (1600 - 320))}
body {font-size: 1.6rem;margin: 0}
h1, h2, h3, h4, h5, h6, p, ul {margin: 0}
ul {padding: 0; list-style-type: none}
a {text-decoration: none;color: #000;display: inline-block}

body {
  background-color: #000;
}

h1 {
  color: goldenrod;
}

textarea {
  resize: none;
  background-color: dimgray;
  border: none;
  transition: all 0.2s;
}

textarea:focus {
  border: none;
  background-color: darkgray;
}

section.messenger {
  width: 80%;
}

.scrollDummy {
  width: 0;
  height: 0;
}

.showMsg {
  width: 100%;
  height: 500px;
  overflow: scroll;
}

ul.oldMsg {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

ul.oldMsg li {
  padding: 1.5rem;
  margin: 0.8rem auto;
  border-radius: 10px;
  background-color: dimgray;
  color: goldenrod;
  font-size: 1.5rem;
  display: inline;
}

form.send {
  display: flex;
  align-items: center;
}

.sendMsg textarea {
  margin-top: 20px;
  width: 90%;
  height: 10rem;
}

.sendMsg button {
  width: 10%;
  height: 10rem;
  margin-top: 20px;
  border: 0px;
  transition: all 0.2s;
}

.sendMsg button:hover,
.sendMsg button:focus {
  filter: brightness(1.3);
}

.sendMsg button:active {
  filter: brightness(.7);
}

.showMsg::-webkit-scrollbar-track,
.showMsg::-moz-scrollbar-track {
  background-color: #000;
}